.App {
  width: 100%;
  text-align: center;

}


/* keep Navbar from collapsing */
.Header::after { 
  content: " ";
  display: block; 
  height: 0; 
  clear: both;
}


.ContentArea{
    
  display: block;
  opacity: 0;    
  width: 75%;
  margin: 5% 0 0 25%;

  text-align: left;
  padding: 2%;
  /* TODO : remove maybe*/
  font-size: 200%;
}

.ContentArea > div{
  padding: 3% 5%;
  font-family: 'Rubik';
  color : var(--paraText);    
}

.ContentArea h2 {
  margin: 3% 0;
  text-transform: uppercase;
  color: var(--headerText);
  border-bottom: 0.1em dashed var(--headerText);

}

.ContentArea p {
  font-size: 60%;
}

.showMainContent{

  opacity: 1;    
  transition: opacity 0.5s ease-in 0.25s;
  -webkit-transition: opacity 0.5s ease-in 0.25s;
  -moz-transition: opacity 0.5s ease-in 0.25s;
    -o-transition: opacity 0.5s ease-in 0.25s;
}


