}

@media only screen and (max-width: 991px){ 

  
}


@media only screen and (max-width: 768px){
  .ContentArea{
      display: none;
      width: 90%;
      margin: 5% auto;
    /*  text-align: center; */
      opacity: 1;
      font-size: 200%;
  }

  .displayMainContent{
      display: block;
  }    

  .ContentArea h1 {
      margin-bottom: 10%;
  
  }


}
  


@media only screen and (max-width: 479px){ 

.ContentArea p {
  font-size: 50%;
}


}
