.contactArea{
    font-size: 200%;
    padding-bottom: 2%;

}
#socialMediaBox{
    display: flex;
    width: 50%;

}
.SocialMediaLink{
width: 30%;
filter: invert(100) sepia(0) saturate(1) hue-rotate(0deg) brightness(100);
transition:filter 0.5s ease-in 0.5s;

}

.SocialMediaLink.LIGHT{
    filter: invert(0) sepia(0) saturate(0) brightness(0);


}
.SocialMediaLink:hover{
transform: scale(1.5);
}


.SocialMediaLink:nth-child(2) > .MediaIconBox{
    position: relative;
    top: 5%;
}

.MediaIconBox{
    margin: 0% 25%;
}

.MediaIconBox a{
    width: 100%;

    width: 25px;
    display: block;
    padding: 2px;
}

.MediaIconBox img{
    width: 100%;
}


#emailAndPhoneBox{
    width: 120%;
    display: flex;
    font-size: 30%;
    margin-left: -10%;
    margin-right: -10%;
    flex-direction: column;
    margin-top: 10px;
}

#emailAndPhoneBox p{
    margin: 1% 0%;
 
}

#emailAndPhoneBox a:hover{
    text-decoration: underline;
}
#copyrightBox{
    font-size: 30%;

}



@media only screen and (min-width: 992px){


}

@media only screen and (max-width: 991px){ 

}


@media only screen and (max-width: 768px){
    .contactArea{
        font-size: 300%;
        width: 60%;
    }

    #emailAndPhoneBox{
        width: 100%;
        display: flex;
        font-size: 30%;
        margin: 2%;
        padding: 4% 0;
    }
    #emailAndPhoneBox a{
        padding: 4% 0%;
        display: block;
    }    

    .SocialMediaLink{
        padding: 5%;
    }
    
    #socialMediaBox{
        display: flex;
        width: 70%;
    
    }
    .MediaIconBox a{
    
        width: 25px;
        display: block;
        padding: 2px;
    }
    

     }
    


